import React, { useState, useRef } from 'react';
import { Video, Square, Play, Pause, Upload, X } from 'lucide-react';
import '../styles/multimedia.css';

export default function VideoRecorder({ onSubmit, onCancel }) {
  const [isRecording, setIsRecording] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [videoURL, setVideoURL] = useState(null);
  const [videoBlob, setVideoBlob] = useState(null);
  const [recordingTime, setRecordingTime] = useState(0);
  const [isCameraReady, setIsCameraReady] = useState(false);
  
  const mediaRecorderRef = useRef(null);
  const chunksRef = useRef([]);
  const timerRef = useRef(null);
  const videoRef = useRef(null);
  const streamRef = useRef(null);

  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: true, 
        audio: true 
      });
      streamRef.current = stream;
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        setIsCameraReady(true);
      }
    } catch (error) {
      console.error('Error accessing camera:', error);
      alert('Could not access camera/microphone. Please check permissions.');
    }
  };

  const stopCamera = () => {
    if (streamRef.current) {
      streamRef.current.getTracks().forEach(track => track.stop());
      streamRef.current = null;
      setIsCameraReady(false);
    }
  };

  const startRecording = () => {
    if (!streamRef.current) return;

    const mediaRecorder = new MediaRecorder(streamRef.current);
    mediaRecorderRef.current = mediaRecorder;
    chunksRef.current = [];

    mediaRecorder.ondataavailable = (e) => {
      if (e.data.size > 0) {
        chunksRef.current.push(e.data);
      }
    };

    mediaRecorder.onstop = () => {
      const blob = new Blob(chunksRef.current, { type: 'video/webm' });
      const url = URL.createObjectURL(blob);
      setVideoURL(url);
      setVideoBlob(blob);
      stopCamera();
    };

    mediaRecorder.start();
    setIsRecording(true);
    setRecordingTime(0);

    timerRef.current = setInterval(() => {
      setRecordingTime(prev => prev + 1);
    }, 1000);
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
      setIsPaused(false);
      clearInterval(timerRef.current);
    }
  };

  const pauseRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      if (isPaused) {
        mediaRecorderRef.current.resume();
        timerRef.current = setInterval(() => {
          setRecordingTime(prev => prev + 1);
        }, 1000);
      } else {
        mediaRecorderRef.current.pause();
        clearInterval(timerRef.current);
      }
      setIsPaused(!isPaused);
    }
  };

  const handleSubmit = () => {
    if (!videoBlob) {
      alert('Please record video first');
      return;
    }

    const videoFile = new File([videoBlob], `video-${Date.now()}.webm`, { type: 'video/webm' });
    
    if (onSubmit) {
      onSubmit(videoFile);
    }
  };

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <div className="video-recorder-container">
      <h3 className="video-recorder-title">Video Recorder</h3>

      <div className="video-recorder-body">
        {/* Video Preview */}
        <div className="video-preview-area">
          {!videoURL ? (
            <video
              ref={videoRef}
              autoPlay
              muted
              playsInline
              className="video-preview"
            />
          ) : (
            <video
              controls
              src={videoURL}
              className="video-preview"
            />
          )}
        </div>

        {/* Recording Controls */}
        <div className="video-recorder-controls">
          {!isCameraReady && !videoURL && (
            <button onClick={startCamera} className="video-btn video-btn-primary video-btn-lg">
              <Video size={24} />
              <span>Start Camera</span>
            </button>
          )}

          {isCameraReady && !isRecording && !videoURL && (
            <button onClick={startRecording} className="video-btn video-btn-danger video-btn-lg">
              <Video size={24} />
              <span>Start Recording</span>
            </button>
          )}

          {isRecording && (
            <>
              <div className="video-recording-indicator">
                <div className="video-pulse"></div>
                <span>Recording: {formatTime(recordingTime)}</span>
              </div>

              <div className="video-button-group">
                <button onClick={pauseRecording} className="video-btn video-btn-warning">
                  {isPaused ? <Play size={20} /> : <Pause size={20} />}
                  <span>{isPaused ? 'Resume' : 'Pause'}</span>
                </button>

                <button onClick={stopRecording} className="video-btn video-btn-danger">
                  <Square size={20} />
                  <span>Stop</span>
                </button>
              </div>
            </>
          )}

          {videoURL && (
            <div className="video-button-group">
              <button onClick={handleSubmit} className="video-btn video-btn-success">
                <Upload size={20} />
                <span>Submit Video</span>
              </button>
              <button 
                onClick={() => {
                  setVideoURL(null);
                  setVideoBlob(null);
                  setRecordingTime(0);
                  startCamera();
                }} 
                className="video-btn video-btn-secondary"
              >
                Record Again
              </button>
            </div>
          )}
        </div>
      </div>

      {onCancel && (
        <div className="video-recorder-footer">
          <button 
            onClick={() => {
              stopCamera();
              onCancel();
            }} 
            className="video-btn video-btn-secondary"
          >
            <X size={16} />
            <span>Cancel</span>
          </button>
        </div>
      )}
    </div>
  );
}