import React, { useState, useRef } from 'react';
import { FileText, FileCode, Mic, Video } from 'lucide-react';
import MarkdownEditor from './MarkdownEditor';
import AudioRecorder from './AudioRecorder';
import VideoRecorder from './VideoRecorder';
import { MEDIA_TYPES } from '../constants/mediaTypes';
import '../styles/multimedia.css';

export default function MultimediaEditor({ onSubmit, onCancel, initialType = 'TEXT' }) {
  const [selectedType, setSelectedType] = useState(initialType);
  const [textContent, setTextContent] = useState('');
  const [showEditor, setShowEditor] = useState(false);

  const handleTextSubmit = () => {
    if (!textContent.trim()) {
      alert('Please enter some text');
      return;
    }
    if (onSubmit) {
      onSubmit({ type: MEDIA_TYPES.TEXT, content: textContent });
    }
  };

  const handleMarkdownSubmit = (content) => {
    if (onSubmit) {
      onSubmit({ type: MEDIA_TYPES.MARKDOWN, content });
    }
    setShowEditor(false);
  };

  const handleAudioSubmit = (audioFile) => {
    if (onSubmit) {
      onSubmit({ type: MEDIA_TYPES.AUDIO, file: audioFile });
    }
    setShowEditor(false);
  };

  const handleVideoSubmit = (videoFile) => {
    if (onSubmit) {
      onSubmit({ type: MEDIA_TYPES.VIDEO, file: videoFile });
    }
    setShowEditor(false);
  };

  const renderEditor = () => {
    switch (selectedType) {
      case MEDIA_TYPES.TEXT:
        return (
          <div className="multimedia-editor-section">
            <label className="multimedia-label">Enter your text:</label>
            <textarea
              value={textContent}
              onChange={(e) => setTextContent(e.target.value)}
              placeholder="Type your text here..."
              className="multimedia-textarea"
            />
            <div className="multimedia-button-group">
              <button onClick={handleTextSubmit} className="multimedia-btn multimedia-btn-primary">
                Submit Text
              </button>
              {onCancel && (
                <button onClick={onCancel} className="multimedia-btn multimedia-btn-secondary">
                  Cancel
                </button>
              )}
            </div>
          </div>
        );

      case MEDIA_TYPES.MARKDOWN:
        return showEditor ? (
          <MarkdownEditor
            onSubmit={handleMarkdownSubmit}
            onCancel={() => setShowEditor(false)}
          />
        ) : (
          <div className="multimedia-editor-section">
            <p className="multimedia-hint">Click the button below to open the Markdown editor.</p>
            <button onClick={() => setShowEditor(true)} className="multimedia-btn multimedia-btn-primary">
              Open Markdown Editor
            </button>
            {onCancel && (
              <button onClick={onCancel} className="multimedia-btn multimedia-btn-secondary">
                Cancel
              </button>
            )}
          </div>
        );

      case MEDIA_TYPES.AUDIO:
        return showEditor ? (
          <AudioRecorder
            onSubmit={handleAudioSubmit}
            onCancel={() => setShowEditor(false)}
          />
        ) : (
          <div className="multimedia-editor-section">
            <p className="multimedia-hint">Click the button below to open the Audio recorder.</p>
            <button onClick={() => setShowEditor(true)} className="multimedia-btn multimedia-btn-danger">
              Open Audio Recorder
            </button>
            {onCancel && (
              <button onClick={onCancel} className="multimedia-btn multimedia-btn-secondary">
                Cancel
              </button>
            )}
          </div>
        );

      case MEDIA_TYPES.VIDEO:
        return showEditor ? (
          <VideoRecorder
            onSubmit={handleVideoSubmit}
            onCancel={() => setShowEditor(false)}
          />
        ) : (
          <div className="multimedia-editor-section">
            <p className="multimedia-hint">Click the button below to open the Video recorder.</p>
            <button onClick={() => setShowEditor(true)} className="multimedia-btn multimedia-btn-purple">
              Open Video Recorder
            </button>
            {onCancel && (
              <button onClick={onCancel} className="multimedia-btn multimedia-btn-secondary">
                Cancel
              </button>
            )}
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <div className="multimedia-editor-container">
      <div className="multimedia-editor-header">
        <h3 className="multimedia-editor-title">Select Content Type</h3>
        <div className="multimedia-type-buttons">
          <button
            onClick={() => {
              setSelectedType(MEDIA_TYPES.TEXT);
              setShowEditor(false);
            }}
            className={`multimedia-type-btn ${selectedType === MEDIA_TYPES.TEXT ? 'active' : ''}`}
            title="Text"
          >
            <FileText size={20} />
          </button>

          <button
            onClick={() => {
              setSelectedType(MEDIA_TYPES.MARKDOWN);
              setShowEditor(false);
            }}
            className={`multimedia-type-btn ${selectedType === MEDIA_TYPES.MARKDOWN ? 'active' : ''}`}
            title="Markdown"
          >
            <FileCode size={20} />
          </button>

          <button
            onClick={() => {
              setSelectedType(MEDIA_TYPES.AUDIO);
              setShowEditor(false);
            }}
            className={`multimedia-type-btn ${selectedType === MEDIA_TYPES.AUDIO ? 'active' : ''}`}
            title="Audio"
          >
            <Mic size={20} />
          </button>

          <button
            onClick={() => {
              setSelectedType(MEDIA_TYPES.VIDEO);
              setShowEditor(false);
            }}
            className={`multimedia-type-btn ${selectedType === MEDIA_TYPES.VIDEO ? 'active' : ''}`}
            title="Video"
          >
            <Video size={20} />
          </button>
        </div>
      </div>

      <div className="multimedia-editor-body">
        {renderEditor()}
      </div>
    </div>
  );
}