import React, { useState } from 'react';
import { Eye, Code, Save, X } from 'lucide-react';
import MarkdownPlayer from './MarkdownPlayer';
import '../styles/multimedia.css';

export default function MarkdownEditor({ onSubmit, onCancel, initialContent = '' }) {
  const [content, setContent] = useState(initialContent);
  const [previewMode, setPreviewMode] = useState(false);

  const handleSubmit = () => {
    if (!content.trim()) {
      alert('Please enter some markdown content');
      return;
    }
    if (onSubmit) {
      onSubmit(content);
    }
  };

  return (
    <div className="markdown-editor-container">
      <div className="markdown-editor-header">
        <h3 className="markdown-editor-title">Markdown Editor</h3>
        <div className="markdown-editor-actions">
          <button
            onClick={() => setPreviewMode(!previewMode)}
            className={`markdown-btn ${previewMode ? 'markdown-btn-secondary' : 'markdown-btn-outline'}`}
          >
            {previewMode ? <Code size={16} /> : <Eye size={16} />}
            <span>{previewMode ? 'Edit' : 'Preview'}</span>
          </button>
        </div>
      </div>

      <div className="markdown-editor-body">
        {previewMode ? (
          <div className="markdown-preview">
            <MarkdownPlayer content={content} />
          </div>
        ) : (
          <textarea
            value={content}
            onChange={(e) => setContent(e.target.value)}
            placeholder="Enter your markdown content here..."
            className="markdown-textarea"
          />
        )}
      </div>

      <div className="markdown-editor-footer">
        <button onClick={handleSubmit} className="markdown-btn markdown-btn-primary">
          <Save size={16} />
          <span>Submit Markdown</span>
        </button>
        {onCancel && (
          <button onClick={onCancel} className="markdown-btn markdown-btn-secondary">
            <X size={16} />
            <span>Cancel</span>
          </button>
        )}
      </div>
    </div>
  );
}