import React from 'react';
import MarkdownPlayer from './MarkdownPlayer';
import AudioPlayer from './AudioPlayer';
import VideoPlayer from './VideoPlayer';
import { FileText } from 'lucide-react';
import { MEDIA_TYPES } from '../constants/mediaTypes';
import '../styles/multimedia.css';

export default function MultimediaPlayer({ mediaType, content, mediaUrl, file }) {
  const renderPlayer = () => {
    switch (mediaType) {
      case MEDIA_TYPES.TEXT:
        return (
          <div className="multimedia-player-text">
            <div className="multimedia-player-text-content">
              {content}
            </div>
          </div>
        );

      case MEDIA_TYPES.MARKDOWN:
        return (
          <MarkdownPlayer 
            content={content}
            mediaUrl={mediaUrl}
          />
        );

      case MEDIA_TYPES.AUDIO:
        return (
          <AudioPlayer 
            src={mediaUrl || (file ? URL.createObjectURL(file) : null)}
          />
        );

      case MEDIA_TYPES.VIDEO:
        return (
          <VideoPlayer 
            src={mediaUrl || (file ? URL.createObjectURL(file) : null)}
            type="video"
          />
        );

      default:
        return (
          <div className="multimedia-player-error">
            <p>Unknown media type: {mediaType}</p>
          </div>
        );
    }
  };

  return (
    <div className="multimedia-player-container">
      {renderPlayer()}
    </div>
  );
}