import React, { useState, useRef } from 'react';
import { Mic, Square, Play, Pause, Upload, X } from 'lucide-react';
import '../styles/multimedia.css';

export default function AudioRecorder({ onSubmit, onCancel }) {
  const [isRecording, setIsRecording] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [audioURL, setAudioURL] = useState(null);
  const [audioBlob, setAudioBlob] = useState(null);
  const [recordingTime, setRecordingTime] = useState(0);
  
  const mediaRecorderRef = useRef(null);
  const chunksRef = useRef([]);
  const timerRef = useRef(null);
  const audioRef = useRef(null);

  const startRecording = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      const mediaRecorder = new MediaRecorder(stream);
      mediaRecorderRef.current = mediaRecorder;
      chunksRef.current = [];

      mediaRecorder.ondataavailable = (e) => {
        if (e.data.size > 0) {
          chunksRef.current.push(e.data);
        }
      };

      mediaRecorder.onstop = () => {
        const blob = new Blob(chunksRef.current, { type: 'audio/webm' });
        const url = URL.createObjectURL(blob);
        setAudioURL(url);
        setAudioBlob(blob);
        stream.getTracks().forEach(track => track.stop());
      };

      mediaRecorder.start();
      setIsRecording(true);
      setRecordingTime(0);

      timerRef.current = setInterval(() => {
        setRecordingTime(prev => prev + 1);
      }, 1000);
    } catch (error) {
      console.error('Error accessing microphone:', error);
      alert('Could not access microphone. Please check permissions.');
    }
  };

  const stopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
      setIsRecording(false);
      setIsPaused(false);
      clearInterval(timerRef.current);
    }
  };

  const pauseRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      if (isPaused) {
        mediaRecorderRef.current.resume();
        timerRef.current = setInterval(() => {
          setRecordingTime(prev => prev + 1);
        }, 1000);
      } else {
        mediaRecorderRef.current.pause();
        clearInterval(timerRef.current);
      }
      setIsPaused(!isPaused);
    }
  };

  const handleSubmit = () => {
    if (!audioBlob) {
      alert('Please record audio first');
      return;
    }

    const audioFile = new File([audioBlob], `audio-${Date.now()}.webm`, { type: 'audio/webm' });
    
    if (onSubmit) {
      onSubmit(audioFile);
    }
  };

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
  };

  return (
    <div className="audio-recorder-container">
      <h3 className="audio-recorder-title">Audio Recorder</h3>

      <div className="audio-recorder-body">
        {/* Recording Controls */}
        <div className="audio-recorder-controls">
          {!isRecording && !audioURL && (
            <button onClick={startRecording} className="audio-btn audio-btn-danger audio-btn-lg">
              <Mic size={24} />
              <span>Start Recording</span>
            </button>
          )}

          {isRecording && (
            <>
              <div className="audio-recording-indicator">
                <div className="audio-pulse"></div>
                <span>Recording: {formatTime(recordingTime)}</span>
              </div>

              <div className="audio-button-group">
                <button onClick={pauseRecording} className="audio-btn audio-btn-warning">
                  {isPaused ? <Play size={20} /> : <Pause size={20} />}
                  <span>{isPaused ? 'Resume' : 'Pause'}</span>
                </button>

                <button onClick={stopRecording} className="audio-btn audio-btn-danger">
                  <Square size={20} />
                  <span>Stop</span>
                </button>
              </div>
            </>
          )}

          {/* Audio Preview */}
          {audioURL && !isRecording && (
            <div className="audio-preview">
              <audio ref={audioRef} controls src={audioURL} className="audio-player" />
              <div className="audio-button-group">
                <button onClick={handleSubmit} className="audio-btn audio-btn-success">
                  <Upload size={20} />
                  <span>Submit Audio</span>
                </button>
                <button 
                  onClick={() => {
                    setAudioURL(null);
                    setAudioBlob(null);
                    setRecordingTime(0);
                  }} 
                  className="audio-btn audio-btn-secondary"
                >
                  Record Again
                </button>
              </div>
            </div>
          )}
        </div>
      </div>

      {onCancel && (
        <div className="audio-recorder-footer">
          <button onClick={onCancel} className="audio-btn audio-btn-secondary">
            <X size={16} />
            <span>Cancel</span>
          </button>
        </div>
      )}
    </div>
  );
}